
.vie-title-area {
  text-align: center;
}
.flex-demo {
  background: #20b907;
  color: #ffffff;
  text-align: center;
  border-radius: 8px;
  height: 100%;
  line-height: 1.5;
}
.flex-container {
  height: 100%;
  overflow-y: scroll;
}
.flex-container .inline {
  width: 2.2rem;
  overflow: hidden;
}
.flex-container .average {
  width: 20%;
  overflow: hidden;
}
.flex-container .small {
  height: 40px;
}



